<template>
    <div>
        <div class="container-fluid">
            <div class="content">
                <div class="head m-3">
                    <router-link to="/login"><b-icon class="icon" icon="chevron-left" font-scale="1.5"/></router-link><h4 style="font-weight: bold;">Register</h4>
                </div>
                <p style="margin-left: 40px;">Let's create your account!</p>
                <div class="form">
                    <form action="" @submit.prevent="register()">
                        <p style="color: #848484;">Name</p>
                        <input type="text" placeholder="Enter your name here" v-model="dataUser.username">
                        <p class="mt-3" style="color: #848484;">Email</p>
                        <input type="email" placeholder="Enter your email here" v-model="dataUser.email">
                        <p class="mt-3" style="color: #848484;">Password</p>
                        <input type="password" id="password" autocomplete="on" placeholder="Enter your password here" v-model="dataUser.password"><img class="btn eye" id="eye" src="../assets/icon/visibility.png" @click="seePassword()"/>
                        <button>Register</button>
                    </form>
                    <div class="alternate">
                        <h6><span>Register with</span></h6>
                    </div>
                    <div class="alternate">
                    <button><b-icon icon="google"/>  Google</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { mapActions } from 'vuex'

export default {
  data () {
    return {
      dataUser: {
        username: '',
        email: '',
        password: ''
      }
    }
  },
  methods: {
    ...mapActions({
      actionRegister: 'register/registerAccount'
    }),
    seePassword () {
      const x = document.getElementById('password')
      const y = document.getElementById('eye')
      if (x.type === 'password') {
        x.type = 'text'
        y.src = 'data:image/png;base64,'
      } else {
        x.type = 'password'
        y.src = 'data:image/png;base64,'
      }
    },
    register () {
      if (!this.dataUser.username || !this.dataUser.email || !this.dataUser.password) {
        alert('All data must be filled')
      } else {
        this.actionRegister(this.dataUser).then((response) => {
          alert('Succes register an account')
          this.$router.push('/login')
        }).catch((err) => {
          alert(err)
        })
      }
    }
  }
}
</script>

<style scoped>
    .container-fluid{
        width: 100%;
        height: 100vh;
        background: #E5E5E5;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .content{
        position: relative;
        background: #FFFFFF;
        width: 450px;
        height: 550px;
        border-radius: 15px;
        box-shadow: 0 10px 10px 10px rgba(0,0,0,0.1);
    }
    .content .head{
        display: flex;
        justify-content: center;
        color: #7E98DF;
    }
    .content .head .icon{
        position: absolute;
        left: 30px;
        top: 17px;
    }
    .content .form{
        width: 100%;
        line-height: 1;
    }
    .content .form form{
        padding: 20px 40px 20px 40px;
    }
    .content .form form input{
        margin-top: -10px;
        width: 100%;
        height: 40px;
        outline: 0;
        border-width: 0 0 1px;
        border-color: #000000;
        transition: 1s;
        font-weight: bold;
    }
    .content .form form input:focus{
        border-color: #5eff00;
    }
    .content .form form .eye{
        position: relative;
        float: right;
        transform: translate(20% ,-90%);
        width: 45px;
        height: 35px;
    }
    .content .form form button{
        width: 100%;
        height: 50px;
        border: none;
        border-radius: 25px;
        margin-top: -20px;
        color: #FFFFFF;
        background-color: #7E98DF;
    }
    .content .alternate{
        display: flex;
        justify-content: center;
    }
    .content .alternate h6{
        width: 80%;
        text-align: center;
        border-bottom: 1px solid #848484;
        line-height: 0.1em;
        margin: 10px 0 10px;
        color: #848484;
    }
    .content .alternate h6 span{
        background:#fff;
        padding:0 10px;
    }
    .content .alternate button{
        margin-top: 20px;
        width: 80%;
        height: 50px;
        color: #7E98DF;
        border: 1px solid #7E98DF;
        background-color: #FFFFFF;
        border-radius: 25px;
    }
</style>
